<template>
  <van-nav-bar title="个人中心" />
  <div class="user" @click="goSetUser" @keyup="goSetUser">
    <div>
      <van-image
        round
        width="5rem"
        height="5em"
        :src="user.avatar_url"
      />
    </div>
    <div>
      <h3>{{user.name}}</h3>
      <h5>{{user.email}}</h5>
    </div>
  </div>
  <van-cell title="我的收藏" is-link @click="collection" />
  <van-cell title="我的订单" @click="$router.push({path:'/orderList'})" is-link />
  <van-cell title="账号管理" @click="goSetUser" is-link />
  <van-cell title="地址管理" to="/AddressList" is-link />
  <van-cell title="关于我们" @click="$router.push({path:'/aboutUs'})" is-link />
  <van-button @click="logout" round type="primary">退出登录</van-button>
  <footerS></footerS>
</template>
<script>
import footerS from '@/components/common/footers.vue';
// eslint-disable-next-line import/no-cycle
import { GetUser, PostUserLogout } from '@/network/user';
import { useRouter } from 'vue-router';
import { Toast } from 'vant';
import { onMounted, ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import store from '@/store';

export default {
  components: {
    footerS,
  },
  setup() {
    store.state.loading = true;
    const router = useRouter();
    const logout = () => {
      PostUserLogout().then((res) => {
        if (res.status === 204) {
          window.localStorage.setItem('token', '');
          Toast.success('退出成功');
          router.push({ path: '/login' });
        }
      });
    };
    const user = ref({});
    onMounted(() => {
      GetUser().then((res) => {
        console.log(res);
        user.value = res.data;
        store.state.loading = false;
      });
    });
    function goSetUser() {
      router.push({ path: '/setUser' });
    }
    function collection() {
      router.push({ path: '/collection' });
    }
    return {
      logout,
      user,
      goSetUser,
      collection,
    };
  },

};
</script>
<style>
.user{
  overflow: hidden;
  background-image: linear-gradient(#ff9b27, #fff);
  padding: 2rem 1rem;
}
.user div:first-child{
  float: left;
}
.user div:last-child{
  float: left;
  text-align: left;
  line-height: 1rem;
  padding: 0 0.5rem;
  margin-bottom: -1rem;
}
</style>
