<template>
  <van-nav-bar title="购物车" />
  <van-notice-bar
    left-icon="volume-o"
    text="无论我们能活多久，我们能够享受的只有无法分割的此刻，此外别无其他。"
  />

  <div class="cart-body">
    <van-checkbox-group v-model="checked" @change="setSelectCar" ref="checkboxGroup">
      <van-swipe-cell v-for="item in goodsList" :key="item.id">
        <div class="good-item">
          <van-checkbox :name="item.id" />
          <div class="good-img"><img :src="item.goods.cover_url" :alt="item.goods.title"></div>
          <div class="good-desc">
            <div class="good-title">
              <span>{{item.goods.title}}</span>
              <span>库存：{{item.goods.stock}}</span>
            </div>
            <div class="good-btn">
              <div class="price"><small>¥</small>{{item.goods.price}}</div>
              <van-stepper integer theme="round" button-size="22" disable-input
                           v-model="item.num" :name="item.id" :min="1"
                           @change="updateCarNum"
                           :max="item.goods.stock" async-change/>
            </div>
          </div>
        </div>
        <template #right>
          <van-button
            square
            icon="delete"
            type="danger"
            class="delete-button"
            @click="delCar(item.id)"
          />
        </template>
      </van-swipe-cell>
    </van-checkbox-group>
  </div>

  <van-submit-bar :price="total*100" button-text="提交订单" @submit="onSubmit">
    <van-checkbox v-model="selectAll" @click="toggleAll ">全选</van-checkbox>
  </van-submit-bar>
  <footerS></footerS>
</template>
<script>
import footerS from '@/components/common/footers.vue';
// eslint-disable-next-line import/no-cycle
import {
  getCarsList, DelCar, setCarNum, SetSlect,
} from '@/network/cars';
import { computed, onMounted, ref } from 'vue';
import { Toast } from 'vant';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

export default {
  components: {
    footerS,
  },
  setup() {
    const router = useRouter();
    const store = useStore();
    const checked = ref([]);
    const selectAll = ref(false);
    const checkboxGroup = ref(null);
    const goodsList = ref([]);
    const onSubmit = () => {
      router.push({ path: '/order' });
    };
    const onClickLink = () => Toast('修改地址');
    const init = () => {
      getCarsList().then((res) => {
        goodsList.value = res.data.data;
        console.log(res);
        console.log(goodsList.value);
        checked.value = res.data.data.filter((item) => item.is_checked).map((item) => item.id);
      });
    };
    onMounted(() => {
      init();
    });
    function delCar(id) {
      DelCar(id).then((res) => {
        if (res.status === 204) {
          Toast.success('删除成功');
          console.log(res);
          init();
          store.dispatch('upCarList');
        } else {
          console.log('删除失败');
        }
      });
    }
    function updateCarNum(value, detail) {
      console.log(value);
      console.log(detail);
      setCarNum(detail.name, value).then((res) => {
        console.log(res);
      });
    }
    const setSelectCar = (names) => {
      console.log(names);
      SetSlect({ cart_ids: names }).then((res) => {
        console.log(res);
      });
      if (checked.value.length === goodsList.value.length) {
        selectAll.value = true;
      } else {
        selectAll.value = false;
      }
    };
    const toggleAll = () => {
      if (checked.value.length !== goodsList.value.length) {
        checkboxGroup.value.toggleAll(true);
      } else {
        checkboxGroup.value.toggleAll();
      }
    };
    // eslint-disable-next-line max-len,no-return-assign,no-param-reassign
    const total = computed(() => goodsList.value.filter((i) => checked.value.includes(i.id)).reduce((s, n) => s += n.num * n.goods.price, 0));
    return {
      checked,
      onSubmit,
      onClickLink,
      goodsList,
      updateCarNum,
      delCar,
      setSelectCar,
      toggleAll,
      checkboxGroup,
      selectAll,
      total,
    };
  },
};
</script>

