<template>
  <van-nav-bar title="分类" />
  <NoticeS></NoticeS>
<!--左导航-->
  <van-collapse v-model="activeNames" accordion style="float: left;width: 25%">
  <van-collapse-item v-for="item in slidebar" :key="item.id"
            :title="item.name" :name="item.id">
        <van-sidebar v-model="active">
          <van-sidebar-item
            v-for="val in item.children"
            :key="val.id" :title="val.name"
            @click="getGoodsList(val.id)"
          />
        </van-sidebar>
    </van-collapse-item>
  </van-collapse>
<!--右显示层-->
  <div style="width: 70%;float: right">
    <van-tabs v-model:active="activeName" @click-tab="onClickTab">
      <van-tab title="销量排序" name="sales"></van-tab>
      <van-tab title="价格排序" name="price"></van-tab>
      <van-tab title="评论排序" name="comments_count"></van-tab>
    </van-tabs>
    <!--    商品列表-->
    <van-list
      :finished="finished"
      offset="80"
      finished-text="拉动试试看"
      @load="onLoad"
    >
      <van-card
        v-for="item in goodsShow" :key="item.id"
        tag="畅销"
        :price="item.price"
        :desc="item.updated_at"
        :title="item.title"
        :thumb="item.cover_url"
        :origin-price="item.price+50"
        @click="goDetail(item.id)"
      />
    </van-list>
    </div>
    <van-empty description="商品突然飞走啦" v-show="goodsShow.length<=0" />

  <footerS></footerS>
</template>
<script>
import {
  ref, reactive, computed, onMounted,
} from 'vue';
// eslint-disable-next-line import/extensions
import footerS from '@/components/common/footers.vue';
import NoticeS from '@/components/common/noticeS.vue';
import { useRouter } from 'vue-router';
// eslint-disable-next-line import/no-cycle
import { getAboutAll, getAboutGoods } from '@/network/about';
import { useStore } from 'vuex';

export default {
  components: {
    footerS,
    NoticeS,
  },
  data() {
    return {
      slidebar: [],
    };
  },
  setup() {
    const store = useStore();
    store.state.loading = true;
    const finished = ref(false);
    const Loading = ref(false);
    const active = ref(0);
    const cids = ref(0);
    const sorts = ref('sales');
    const activeNames = ref('1');
    const goods = reactive({
      sales: { page: 1, list: [] },
      price: { page: 1, list: [] },
      comments_count: { page: 1, list: [] },
    });
    const goodsShow = computed(() => goods[sorts.value].list);
    const init = () => {
      getAboutGoods(cids.value, 'sales').then((res) => {
        goods.sales.list = res.data.goods.data;
        store.state.loading = false;
      });
      getAboutGoods(cids.value, 'price').then((res) => {
        goods.price.list = res.data.goods.data;
      });
      getAboutGoods(cids.value, 'comments_count').then((res) => {
        goods.comments_count.list = res.data.goods.data;
      });
    };
    onMounted(() => {
      init();
    });

    function getGoodsList(cid) {
      cids.value = cid;
      init();
    }

    function onClickTab({ name }) {
      sorts.value = name;
    }

    const onLoad = () => {
      Loading.value = true;
      const pages = goods[sorts.value].page;
      getAboutGoods(cids.value, sorts.value, pages).then((res) => {
        goods[sorts.value].list.push(...res.data.goods.data);
        goods[sorts.value].page += 1;
        Loading.value = false;
        if (res.data.goods.data.length < 0) {
          finished.value = true;
        }
      });
    };
    const router = useRouter();

    function goDetail(Goodsid) {
      const id = Goodsid;
      router.push(`/detail/${id}`);
    }

    return {
      goDetail,
      active,
      activeNames,
      getGoodsList,
      goodsShow,
      onClickTab,
      finished,
      onLoad,
    };
  },
  mounted() {
    getAboutAll().then((res) => {
      console.log(res);
      this.slidebar = res.data.categories;
    });
  },
};
</script>
